}

.archive__list-title::before {
  border-bottom: 0.6875rem solid transparent;
  border-left: 1.0625rem solid #408f95;
  border-top: 0.6875rem solid transparent;
  bottom: 0;
  content: "";
  height: 0;
  left: 0.625rem;
