.btn {
  display: inline-flex;
  align-items: center;
  background-color: #408f95;
  font-family: "Gotu", sans-serif;
  max-width: 12.625rem;
  width: 100%;
  color: #ffffff;
  padding: 1.0625rem 2.5rem;
  position: relative;
  z-index: 1;
  cursor: pointer;
  justify-content: center;
  pointer-events: none;
}
@media screen and (min-width: 768px) {
  .btn {
    pointer-events: all;
  }
}

.btn.btn--white {
  background-color: #ffffff;
  color: #408f95;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0.25rem;
  right: -0.25rem;
  height: 100%;
  width: 100%;
  border: 1px solid #408f95;
  z-index: -100;
}

.btn.btn--white::before {
  border-color: #ffffff;
}

.btn__text {
  font-size: 0.875rem;
}

.btn__text-upper {
  text-transform: uppercase;
}

.btn__text::first-letter {
  text-transform: uppercase;
}

.btn__arrow {
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  height: 0.375rem;
  margin-left: 0.5rem;
  transform: skew(45deg);
  width: 2.5rem;
  margin-top: -0.25rem;
}

.btn__arrow.btn__text--white {
  border-color: #408f95;
}

.btn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: #ffffff;
  border: 1px solid #408f95;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -10;
}

.btn.btn--white::after {
  background: #408f95;
  border-color: #ffffff;
}

@media screen and (min-width: 768px) {
  .btn:hover {
    color: #408f95;
    opacity: 1;
    z-index: 10;
  }
}

@media screen and (min-width: 768px) {
  .btn.btn--white:hover {
    color: #ffffff;
  }
}

.btn:hover::after {
  transform: scale(1, 1);
}

.btn:hover .btn__arrow {
  border-color: #408f95;
}

.btn.btn--white:hover .btn__arrow {
  border-color: #ffffff;
}

