@use "global" as *;

.breadcrumb {
  font-size: rem(14);
}

.breadcrumb__content {
  display: flex;
  gap: rem(10);
}

.breadcrumb__content span a:hover {
  opacity: .6;
}

.breadcrumb__between {
  position: relative;
}

.breadcrumb__between::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%) rotate(-45deg);
  border-right: 1px solid $black2;
  border-bottom: 1px solid $black2;
  width: rem(6);
  height: rem(6);
}

.breadcrumb__between--white::before {
  border-right: 1px solid $black2;
  border-bottom: 1px solid $black2;
}