@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP", sans-serif;
$english-family: "Gotu", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
  position: relative;
}

.no_scroll {
  overflow: hidden;
}

// *,
// *::before,
// *::after {
//   outline: 1px solid red;
// }


// フォントウェイト
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #000000;
$black2: #0d2936;
$white: #ffffff;
$green: #408f95;
$green2: #0d2936;
$green3: #ddf0f1;
$brown: #796a64;
$red: #c94800;
$gray: #d9d9d9;
